import { createRouter, createWebHistory } from 'vue-router';

const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: '/', name: 'IndexManage', component: () => import('@/pages/Login'), meta: { title: '' } },
    { path: '/login', name: 'LoginManage', component: () => import('@/pages/Login'), meta: { title: '' } },
    { path: '/chat', name: 'ChatManage', component: () => import('@/components/chat/Main'), meta: { title: '' } },
    {
      path: '/admin',
      name: 'AdminManage',
      component: () => import('@/pages/Home'),
      children: [
        /*  */
        { path: 'home/index/:access_token?', name: 'HomeManage', component: () => import('@/pages/Index'), meta: { title: '' } },
        /*  */
        { path: 'auth/index', name: 'AuthManage', component: () => import('@/pages/permission/Auth'), meta: { title: '' } },
        { path: 'role/index', name: 'RoleManage', component: () => import('@/pages/permission/Role'), meta: { title: '' } },
        { path: 'permission/index', name: 'PermissionApply', component: () => import('@/pages/permission/Apply'), meta: { title: '' } },
        /*  */
        { path: 'file/index', name: 'FileManage', component: () => import('@/pages/File'), meta: { title: '' } },
        /*  */
        { path: 'push/index', name: 'PushManage', component: () => import('@/pages/system/Push'), meta: { title: '' } },
        { path: 'config/index', name: 'SystemConfigManage', component: () => import('@/pages/system/Config'), meta: { title: '' } },
        { path: 'database/index', name: 'DatabaseManage', component: () => import('@/pages/system/Database'), meta: { title: '' } },
        { path: 'log/index', name: 'SystemLogManage', component: () => import('@/pages/system/Log'), meta: { title: '' } },
        { path: 'area/index', name: 'AreaManage', component: () => import('@/pages/system/Area'), meta: { title: '' } },
        { path: 'tools/index', name: 'SystemTools', component: () => import('@/pages/system/Tools'), meta: { title: '' } },
        /*  */
        { path: 'users/index', name: 'UsersManage', component: () => import('@/pages/users/Users'), meta: { title: '' } },
        { path: 'userCenter/index', name: 'CenterManage', component: () => import('@/pages/users/Center'), meta: { title: '' } },
        { path: 'oauth/index', name: 'OAuthManage', component: () => import('@/pages/users/OAuth'), meta: { title: '' } },
        /*  */
        { path: 'interfaceCategory/index', name: 'InterfaceManage', component: () => import('@/pages/Interface'), meta: { title: '' } },
        /*  */
        { path: 'spider/index', name: 'SpiderManage', component: () => import('@/pages/Spider'), meta: { title: '' } }

      ]
    }
  ]
});
export default router;
