export default {
  /* todo: */
  theme(state) {
    return state.theme || 'primary'
  },
  /**
   * todo:
   * @param state
   * @return {string}
   */
  activeColor(state) {
    let color = '';
    state.themeAttr.forEach((item) => {
      if (item.theme === state.theme) {
        color = item.color;
      }
    })
    return color;
  }
}
